            </div>
        </div>
        <!-- 退出登录 -->
        <van-cell title="退出登录" @click="show = true" class="el-button" />
        <van-action-sheet
            v-model="show"
            @select="onSelect"
            :actions="actions"
            cancel-text="取消"
            close-on-click-action
        />
    </div>
