<script>
import { userInfo } from '@/api/api';
import { mapMutations } from 'vuex';
import Vue from 'vue';
import { ActionSheet, Cell, Notify, Toast, Dialog } from 'vant';
import 'vant/lib/index.css';

Vue.use(ActionSheet);
Vue.use(Notify);
Vue.use(Toast);
Vue.use(Cell);
Vue.use(Dialog);

export default {
    data() {
        return {
            userInfo: {},
            show: false,
            actions: [{ name: '退出登录', color: '#ee0a24' }],
        };
    },
    async mounted() {
        let _token = this.$store.state._token;
        if (_token) {
            let ret = await userInfo(_token);
            this.userInfo = ret.data.user_info;
        }

        if (this.userInfo.timeOdl == false) {
            Dialog.alert({
                title: '登录已过期',
                message: '请重新登录！',
                theme: 'round-button',
            }).then(() => {
                localStorage.setItem('_token', '');
                this.$router.go(0);
            });
        }
        let toK = localStorage.getItem('_token');
        if (toK == '') {
            localStorage.removeItem('clickName');
            localStorage.removeItem('clickId');
        }
    },
    methods: {
        ...mapMutations(['getTime']),

        login: function() {
            if (!this.userInfo.userId) {
                this.$router.push({ path: '/login' });
            }
        },
        onSelect(item) {
            // 退出登录
            if (!this.userInfo.userId) {
                Notify({ type: 'primary', message: '您还未登录' });
            } else {
                localStorage.removeItem('_token');
                this.$router.go(0);

                Toast(item.name);
            }
        },
    },
};
</script>

